  font-style: normal;
  font-weight: 400;
  line-height: 1.33333 px;
  letter-spacing: -.02em;
  color: rgba(17, 17, 17, .6);
  transition: all 250ms var(--transition-timing-function);
  text-decoration: none;
}

.categories__link:hover {
  text-transform: uppercase;
